import React, { useState, useEffect } from 'react';
import { useAuth } from '../../common/context/AuthContext';
import { useUnifiedToast } from '../../common/services/unifiedToastService';
import { supabase } from '../../common/services/supabaseClient';
import { SkeletonFullPage } from '../../promoter/components/skeletons';
import { useSearchParams } from 'react-router-dom';
import {
  CheckCircle,
  XCircle,
  Eye,
  Clock,
  User,
  Mail,
  Phone,
  MapPin,
  CreditCard,
  FileText,
  AlertCircle,
  Calendar,
  Banknote,
  ChevronLeft,
  ChevronRight,
  Search
} from 'lucide-react';
import {
  UnifiedBackground,
  UnifiedCard,
  useScrollAnimation,
  UnifiedButton,
  Typography,
  Container,
  SharedStyles
} from "../../common/components/SharedTheme";

export default function KYCApproval() {
  const { user } = useAuth();
  const { showSuccess, showError } = useUnifiedToast();
  useScrollAnimation();
  const [searchParams, setSearchParams] = useSearchParams();

  const [activeTab, setActiveTab] = useState(searchParams.get('tab') || 'kyc');
  const [submissions, setSubmissions] = useState([]);
  const [loading, setLoading] = useState(true);
  const [selectedSubmission, setSelectedSubmission] = useState(null);
  const [processing, setProcessing] = useState(false);
  const [searchTerm, setSearchTerm] = useState('');

  // Update URL when tab changes
  const handleTabChange = (tab) => {
    setActiveTab(tab);
    setSearchParams({ tab });
  };

  useEffect(() => {
    const tab = searchParams.get('tab');
    if (tab && (tab === 'kyc' || tab === 'bank')) {
      setActiveTab(tab);
    }
  }, [searchParams]);

  useEffect(() => {
    loadPendingSubmissions();
  }, [activeTab]);

  // Filter submissions based on search term
  const filteredSubmissions = submissions.filter(submission => {
    if (!searchTerm) return true;
    const searchLower = searchTerm.toLowerCase();
    return (
      submission.promoter_details?.promoter_id?.toLowerCase().includes(searchLower) ||
      submission.promoter_details?.name?.toLowerCase().includes(searchLower) ||
      submission.promoter_name?.toLowerCase().includes(searchLower)
    );
  });

  const loadPendingSubmissions = async () => {
    try {
      setLoading(true);
      setSubmissions([]);
      setSelectedSubmission(null);

      const apiUrl = process.env.REACT_APP_API_URL || 'http://localhost:5001/api';
      const endpoint = activeTab === 'kyc' ? '/kyc/pending' : '/bank-details/pending';

      const response = await fetch(`${apiUrl}${endpoint}`);
      if (!response.ok) {
        throw new Error(`Failed to fetch ${activeTab} submissions`);
      }

      const result = await response.json();

      if (result.success && Array.isArray(result.data)) {
        await processSubmissions(result.data);
      } else {
        setSubmissions([]);
      }
    } catch (error) {
      showError(`Failed to load ${activeTab} submissions`);
      setSubmissions([]);
    } finally {
      setLoading(false);
    }
  };

  const processSubmissions = async (submissionsArray) => {
    try {
      // Enhance submissions with essential promoter details only
      const enhancedSubmissions = await Promise.all(
        submissionsArray.map(async (submission) => {
          try {
            const { data: promoter, error: promoterError } = await supabase
              .from('profiles')
              .select('id, name, promoter_id, email, phone')
              .eq('id', submission.promoter_id)
              .single();

            return {
              ...submission,
              promoter_details: promoter || null,
              promoter_error: promoterError
            };
          } catch (err) {
            return {
              ...submission,
              promoter_details: null,
              promoter_error: err
            };
          }
        })
      );

      setSubmissions(enhancedSubmissions);
    } catch (error) {
      setSubmissions([]);
    }
  };

  const handleReview = async (status) => {
    if (!selectedSubmission) return;

    try {
      setProcessing(true);
      const apiUrl = process.env.REACT_APP_API_URL || 'http://localhost:5001/api';
      const endpoint = activeTab === 'kyc' ? '/kyc/review' : '/bank-details/review';

      const payload = {
        submission_id: selectedSubmission.id,
        status: status,
        reviewer_id: user.id || user.promoter_id,
        review_notes: '' // Empty review notes since we removed the field
      };

      const response = await fetch(`${apiUrl}${endpoint}`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(payload)
      });

      const result = await response.json();

      if (result.success) {
        showSuccess(`${activeTab.toUpperCase()} ${status} successfully!`);
        setSelectedSubmission(null);
        loadPendingSubmissions();
      } else {
        showError(result.error || `Failed to review ${activeTab}`);
      }
    } catch (error) {
      showError(`An error occurred during review`);
    } finally {
      setProcessing(false);
    }
  };

  if (loading && submissions.length === 0) {
    return (
      <>
        <SharedStyles />
        <UnifiedBackground>
          <SkeletonFullPage type="approval" />
        </UnifiedBackground>
      </>
    );
  }

  return (
    <UnifiedBackground>
      <SharedStyles />
      <Container size="full" className="pt-40 pb-8 px-4 md:px-8 lg:px-12">
        <div className="container mx-auto">
          <div className="flex flex-col md:flex-row md:items-center md:justify-between mb-6">
            <div>
              <h1 className="text-2xl font-bold text-white">Approval Management</h1>
              <p className="text-slate-400">Review and approve KYC and bank detail submissions</p>
            </div>
            <div className="mt-4 md:mt-0">
              {/* Tab Switcher */}
              <div className="flex bg-slate-800/50 p-1 rounded-xl border border-slate-700/50">
                <button
                  onClick={() => handleTabChange('kyc')}
                  className={`px-4 py-2 rounded-lg transition-all duration-300 flex items-center text-sm font-medium ${activeTab === 'kyc' ? 'bg-orange-500 text-white shadow-md' : 'text-slate-300 hover:text-white hover:bg-slate-700/50'
                    }`}
                >
                  <FileText className="w-4 h-4 mr-2" />
                  KYC Submissions
                </button>
                <button
                  onClick={() => handleTabChange('bank')}
                  className={`px-4 py-2 rounded-lg transition-all duration-300 flex items-center text-sm font-medium ${activeTab === 'bank' ? 'bg-orange-500 text-white shadow-md' : 'text-slate-300 hover:text-white hover:bg-slate-700/50'
                    }`}
                >
                  <Banknote className="w-4 h-4 mr-2" />
                  Bank Details
                </button>
              </div>
            </div>
          </div>

          {/* Search and Filter */}
          <div className="bg-slate-800/50 rounded-xl p-4 mb-6 border border-slate-700/50">
            <div className="flex flex-col lg:flex-row gap-4 items-start lg:items-center justify-between">
              <div className="flex-1 w-full">
                <div className="relative">
                  <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                    <Search className="h-4 w-4 text-slate-400" />
                  </div>
                  <input
                    type="text"
                    placeholder={`Search ${activeTab} submissions by promoter name, ID...`}
                    value={searchTerm}
                    onChange={(e) => setSearchTerm(e.target.value)}
                    className="block w-full pl-10 pr-3 py-2 border border-slate-600 rounded-lg bg-slate-700/50 text-white placeholder-slate-400 focus:outline-none focus:ring-2 focus:ring-orange-500 focus:border-orange-500"
                  />
                </div>
              </div>

              <div className="flex items-center gap-2">
                <div className="px-3 py-1 bg-slate-700/50 rounded-lg border border-slate-600">
                  <span className="text-sm text-slate-300">
                    {filteredSubmissions.length} {activeTab === 'kyc' ? 'KYC' : 'Bank'} Pending
                  </span>
                </div>
              </div>
            </div>
          </div>

          <div className="grid grid-cols-1 lg:grid-cols-12 gap-8">
            {/* Left Column - Submissions List */}
            <div className="lg:col-span-4 scroll-animate animate-slide-right stagger-1">
              <UnifiedCard variant="glassDark" className="h-[calc(100vh-250px)] flex flex-col">
                <div className="flex items-center justify-between mb-6">
                  <Typography variant="h5" color="white" className="flex items-center">
                    <Clock className="w-5 h-5 mr-3 text-orange-400" />
                    {activeTab === 'kyc' ? 'Pending KYC' : 'Pending Bank'}
                  </Typography>
                  <div className="px-3 py-1 bg-orange-500/20 rounded-full border border-orange-500/30">
                    <Typography variant="caption" color="orange-400" weight="bold">
                      {filteredSubmissions.length} Total
                    </Typography>
                  </div>
                </div>

                {filteredSubmissions && filteredSubmissions.length > 0 ? (
                  <div className="space-y-3 overflow-y-auto pr-2 custom-scrollbar flex-1">
                    {filteredSubmissions.map((submission) => (
                      <div
                        key={submission.id}
                        onClick={() => setSelectedSubmission(submission)}
                        className={`p-4 rounded-xl border transition-all duration-300 cursor-pointer group hover-lift ${selectedSubmission?.id === submission.id
                          ? 'border-orange-500/50 bg-orange-500/10 shadow-lg shadow-orange-500/10'
                          : 'border-slate-800 hover:border-slate-700 bg-slate-900/40 hover:bg-slate-800/60'
                          }`}
                      >
                        <div className="flex items-center justify-between mb-2">
                          <Typography variant="h6" color="white" className="group-hover:text-orange-300 font-bold transition-colors">
                            {submission.promoter_details?.promoter_id || 'N/A'}
                          </Typography>
                          <Typography variant="caption" color="slate-500">
                            {new Date(submission.submitted_at).toLocaleDateString()}
                          </Typography>
                        </div>
                        <div className="flex items-center text-xs text-slate-500">
                          <User className="w-3 h-3 mr-2" />
                          {submission.promoter_details?.name || submission.promoter_name || 'N/A'}
                        </div>
                      </div>
                    ))}
                  </div>
                ) : (
                  <div className="flex flex-col items-center justify-center py-20 text-center flex-1">
                    <div className="w-20 h-20 bg-slate-800/50 rounded-full flex items-center justify-center mb-4">
                      {searchTerm ? (
                        <Search className="w-10 h-10 text-slate-700" />
                      ) : (
                        <CheckCircle className="w-10 h-10 text-slate-700" />
                      )}
                    </div>
                    <Typography variant="h6" color="slate-500">
                      {searchTerm ? 'No Results Found' : 'Clean Queue!'}
                    </Typography>
                    <Typography variant="bodySmall" color="slate-700">
                      {searchTerm
                        ? `No ${activeTab} submissions match your search.`
                        : `No pending ${activeTab} reviews.`
                      }
                    </Typography>
                  </div>
                )}
              </UnifiedCard>
            </div>

            {/* Right Column - Submission Details */}
            <div className="lg:col-span-8 scroll-animate animate-slide-left stagger-2">
              {selectedSubmission ? (
                <UnifiedCard variant="glassDark">
                  <div className="flex flex-col md:flex-row md:items-center justify-between gap-4 mb-8">
                    <div className="flex items-center">
                      <div className="w-14 h-14 bg-gradient-to-br from-orange-400 to-red-600 rounded-2xl flex items-center justify-center mr-4 shadow-xl">
                        {activeTab === 'kyc' ? <User className="w-7 h-7 text-white" /> : <Banknote className="w-7 h-7 text-white" />}
                      </div>
                      <div>
                        <Typography variant="h4" color="white">{activeTab === 'kyc' ? 'KYC Review' : 'Bank Review'}</Typography>
                        <Typography variant="bodySmall" color="slate-500">Submitted on {new Date(selectedSubmission.submitted_at).toLocaleString()}</Typography>
                      </div>
                    </div>
                  </div>

                  <div className="space-y-8">
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
                      {/* Common Details */}
                      <div className="space-y-6">
                        <section>
                          <Typography variant="overline" color="orange-500" className="mb-3 block font-bold">Promoter Info</Typography>
                          <div className="bg-slate-900/60 p-5 rounded-2xl border border-slate-800 space-y-4">
                            <div>
                              <label className="text-[10px] text-slate-500 uppercase tracking-widest block mb-1">Name</label>
                              <Typography variant="body" color="white" weight="bold">{selectedSubmission.promoter_name}</Typography>
                            </div>
                            <div>
                              <label className="text-[10px] text-slate-500 uppercase tracking-widest block mb-1">Internal ID</label>
                              <Typography variant="bodySmall" color="orange-300">{selectedSubmission.promoter_details?.promoter_id || 'N/A'}</Typography>
                            </div>
                          </div>
                        </section>

                        {activeTab === 'kyc' ? (
                          <section>
                            <Typography variant="overline" color="orange-500" className="mb-3 block font-bold">Address Details</Typography>
                            <div className="bg-slate-900/60 p-5 rounded-2xl border border-slate-800 flex items-start">
                              <MapPin className="w-4 h-4 text-slate-500 mr-3 mt-1" />
                              <Typography variant="bodySmall" color="white" className="leading-relaxed">
                                {selectedSubmission.address || 'Not Provided'}
                              </Typography>
                            </div>
                          </section>
                        ) : (
                          <section>
                            <Typography variant="overline" color="orange-500" className="mb-3 block font-bold">Branch Info</Typography>
                            <div className="bg-slate-900/60 p-5 rounded-2xl border border-slate-800">
                              <label className="text-[10px] text-slate-500 uppercase tracking-widest block mb-1">Branch Name</label>
                              <Typography variant="body" color="white">{selectedSubmission.branch}</Typography>
                            </div>
                          </section>
                        )}
                      </div>

                      {/* Specific Details */}
                      <div className="space-y-6">
                        {activeTab === 'kyc' ? (
                          <section>
                            <Typography variant="overline" color="orange-500" className="mb-3 block font-bold">Identity Documents</Typography>
                            <div className="bg-slate-900/60 p-5 rounded-2xl border border-slate-800 space-y-6">
                              <div>
                                <div className="flex items-center mb-1.5">
                                  <CreditCard className="w-3 h-3 text-orange-400 mr-2" />
                                  <label className="text-[11px] text-slate-500 uppercase font-bold">Aadhaar Number</label>
                                </div>
                                <Typography variant="h6" color="white" className="tracking-[0.2em] font-mono">
                                  {selectedSubmission.aadhaar_number || 'N/A'}
                                </Typography>
                              </div>
                              <div>
                                <div className="flex items-center mb-1.5">
                                  <FileText className="w-3 h-3 text-orange-400 mr-2" />
                                  <label className="text-[11px] text-slate-500 uppercase font-bold">PAN Number</label>
                                </div>
                                <Typography variant="h6" color="white" className="tracking-[0.2em] font-mono uppercase">
                                  {selectedSubmission.pan_number || 'N/A'}
                                </Typography>
                              </div>
                            </div>
                          </section>
                        ) : (
                          <section>
                            <Typography variant="overline" color="orange-500" className="mb-3 block font-bold">Bank Credentials</Typography>
                            <div className="bg-slate-900/60 p-5 rounded-2xl border border-slate-800 space-y-6">
                              <div>
                                <label className="text-[10px] text-slate-500 uppercase tracking-widest block mb-1">Bank Name</label>
                                <Typography variant="h6" color="white" weight="bold">{selectedSubmission.bank_name}</Typography>
                              </div>
                              <div>
                                <label className="text-[10px] text-slate-500 uppercase tracking-widest block mb-1">Account Holder</label>
                                <Typography variant="body" color="white">{selectedSubmission.account_holder_name}</Typography>
                              </div>
                              <div>
                                <label className="text-[10px] text-slate-500 uppercase tracking-widest block mb-1">Account Number</label>
                                <Typography variant="h6" color="orange-400" className="font-mono">{selectedSubmission.account_number}</Typography>
                              </div>
                              <div>
                                <label className="text-[10px] text-slate-500 uppercase tracking-widest block mb-1">IFSC Code</label>
                                <Typography variant="body" color="white" className="font-mono uppercase">{selectedSubmission.ifsc_code}</Typography>
                              </div>
                            </div>
                          </section>
                        )}
                      </div>
                    </div>

                    {/* Review Section */}
                    <div className="pt-8 border-t border-slate-800">
                      <div className="flex items-center justify-end gap-4">
                        <UnifiedButton
                          variant="ghost"
                          onClick={() => setSelectedSubmission(null)}
                        >
                          Cancel
                        </UnifiedButton>
                        <UnifiedButton
                          variant="glass"
                          onClick={() => handleReview('Rejected')}
                          disabled={processing}
                          className="border-red-500/20 text-red-500 hover:bg-red-500/10 px-8"
                        >
                          Reject
                        </UnifiedButton>
                        <UnifiedButton
                          variant="primary"
                          onClick={() => handleReview('Approved')}
                          disabled={processing}
                          className="px-8 shadow-orange-500/10 hover:shadow-orange-500/20"
                        >
                          {processing ? 'Processing...' : 'Approve'}
                        </UnifiedButton>
                      </div>
                    </div>
                  </div>
                </UnifiedCard>
              ) : (
                <div className="h-full flex flex-col items-center justify-center p-20 text-center bg-slate-900/20 border border-dashed border-slate-800 rounded-[2rem]">
                  <div className="w-20 h-20 bg-slate-800/40 rounded-3xl flex items-center justify-center mb-6">
                    <Eye className="w-10 h-10 text-slate-600" />
                  </div>
                  <Typography variant="h5" color="slate-400">Select an item to review</Typography>
                  <Typography variant="bodySmall" color="slate-600">Pending items from the left pool require your attention.</Typography>
                </div>
              )}
            </div>
          </div>
        </div>
      </Container>
    </UnifiedBackground>
  );
}
